import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Divider from '@mui/material/Divider';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import Chip from '@mui/material/Chip';
import './Style.css'
import Box from '@mui/material/Box';
import Modal from 'react-bootstrap/Modal'
import Button from '@mui/material/Button';
import { useState } from 'react';
import ThumbUpAltIcon from '@mui/icons-material/ThumbUpAlt';
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';
import ChatBubbleIcon from '@mui/icons-material/ChatBubble';
import Comment from './Comment';
import 'bootstrap/dist/css/bootstrap.min.css';
import TextField from '@mui/material/TextField';
import { useFormControl } from '@mui/material/FormControl';
export default function AlignItemsList() {
  const[users,setUsers]=useState([]);
const getStuds=async ()=>{
  const response= await fetch('https://jsonplaceholder.typicode.com/photos');
  setUsers(await response.json());
  
}

React.useEffect(()=>{
getStuds();
},[])
  const [show, setShow] = useState(false);
  users.map((curElem)=>{
    const Pic=curElem.thumbnailUrl;
          })
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
    const[Count,setCount]=React.useState(120);
    const[Feed,setFeed]=useState("");
   React.useEffect(()=>{
localStorage.setItem('Likes',Count)
localStorage.setItem('feedback', Feed)
   },[Count,Feed])
  return (
    <div>
      

     {
      users.map((curElem)=>{
    window.Pics=curElem.thumbnailUrl;
          })
     }
      
 
<List >
    <p className='day' style={{fontSize:'12px'}}>Today</p>

      <ListItem className="items" alignItems="flex-start">
        <ListItemAvatar>
          <Avatar alt="Remy Sharp" src={window.Pics}/>
        </ListItemAvatar>
        <ListItemText
          primary="Saurabh Mantri"
          secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="text.primary"
              >
                Class 4A
              </Typography>
             
            </React.Fragment>
          }
        />
        <span id="chip"> <Chip label="Absent" style={{fontWeight:'bold', backgroundColor:'#FEE2E2', color:'#991B1B'}} /></span>
      </ListItem>
     
      <ListItem className="items" alignItems="flex-start">
        <ListItemAvatar>
          <Avatar alt="Travis Howard" src={window.Pics} />
        </ListItemAvatar>
        <ListItemText
          primary="Saurabh Mantri"
          secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="text.primary"
              >
                <span style={{Fontweight:' bolder', color:'#757373'}}>Class 4A</span>
              </Typography><br></br>
              <p><span className='test'>Unit test</span> <br/>English</p>
            </React.Fragment>
          }
        />
        <span id="chip"> <Chip label="Marks" style={{backgroundColor:'#FEF3C7', color:'#92400E',fontWeight:'bold'}} />  <h1 id="marks">31/40</h1></span>
      </ListItem><br></br>
      <p className='day' style={{fontSize:'12px'}}>Yesterday</p>
      <ListItem className="items" alignItems="flex-start">
        <ListItemAvatar>
          <Avatar alt="Travis Howard" src={window.Pics} />
        </ListItemAvatar>
        <ListItemText
          primary="Saurabh Mantri"
          secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="text.primary"
              >
                <span style={{Fontweight:' bolder', color:'#757373'}}>Class 4A</span>
              </Typography><br></br>
              <p><span className='test' style={{color:'#111827'}}>Due on: Thursday,23 Nov 2021</span></p>
            </React.Fragment>
          }
        />
        <span id="chip"> <Chip label="Fees" style={{backgroundColor:'#D1FAE5', color:'#065F46',fontWeight:'bold'}} /> </span>
      </ListItem>
      <ListItem className="items" alignItems="flex-start">
        <ListItemAvatar>
          <Avatar alt="Travis Howard" src={window.Pics} />
        </ListItemAvatar>
        <ListItemText
          primary="Saurabh Mantri"
          secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="text.primary"
              >
                <span style={{Fontweight:' bolder', color:'#757373'}}>Class 4A</span>
              </Typography><br></br>
              <p><span className='test' style={{color:'#111827'}}>Schools will be closed tommorrow due to rains.<br/>
              Any changes to school reopening will be notified.</span></p>
              <Divider component="li" />
              <p id="full"><ThumbUpAltIcon id="thumbicon" sx={{ color: '#1E40AF' , height:'14px',width:'14px'}} onClick={()=>setCount(Count+1)}></ThumbUpAltIcon><span id="likes">{Count}</span><span id="feedback"><ChatBubbleIcon id="chat"></ChatBubbleIcon>&nbsp;&nbsp;<span  id="submit"><button id="sub" onClick={handleShow}>submit feedback</button></span></span></p>
            </React.Fragment>
          }
        />
        
        <span id="chip"> <Chip id="ch2" label="PTA" style={{backgroundColor:' #DBEAFE', color:'#1E40AF',fontWeight:'bold'}} /> </span><br/>
        <span id="chip"> <Chip id="ch" label="Announcements" style={{backgroundColor:' #DBEAFE', color:'#1E40AF',fontWeight:'bold'}} /> </span><br/>
        
        
    
      </ListItem>
      
    </List>
    <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
        style={{ borderRadius:'10px'}}
        id="mod"
      >
        <Modal.Header >
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
        Only school admin will be able to see your feedback
        <TextField  id="multiline"  id="text" maxRows={4}/>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            CANCEL
          </Button>
    
          <Button id="sharebtn" onClick={()=>setFeed(Feed+TextField.length)} style={{color: '#1E40AF'}} variant="primary">SHARE</Button>
        </Modal.Footer>
      </Modal>






  



        
      
      
    
      
</div>
      
  );


  }